/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/

/* Links */

a {
  color: black;
  text-decoration: none;
  &:visited {
    color: $color__link-visited;
  }
  &:hover,
  &:focus,
  &:active {
    color: $color__link-hover;
  }
  &:focus {
    outline: thin dotted;
  }
  &:hover,
  &:active {
    outline: 0;
  }
}

/* Menus */
.main-navigation li {
  padding-left: 20px;
}

.main-navigation {
  // clear: both;
  //width: 100%;

  ul {
    list-style: none;
    margin: 0;
    padding-left: 0;
    display: flex;
    justify-content: flex-end;

    ul {
      box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
      position: absolute;
      top: $base__space-unit;
      left: -999rem;
      z-index: 99999;

      ul {
        left: -999rem;
        top: 0;
      }

      li {
        &:hover > ul,
        &.focus > ul {
          left: 100%;
        }
      }

      a {
        width: 200px;
      }
    }
    li:hover > ul,
    li.focus > ul {
      left: auto;
    }
  }
  li {
    position: relative;
  }
  a {
    display: block;
    text-decoration: none;
  }
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
  display: block;
}

@media screen and (min-width: 768px) {
  .menu-toggle {
    display: none;
  }
}

.comment-navigation,
.posts-navigation,
.post-navigation {
  .site-main & {
    margin: 0 0 $base__space-unit;
    overflow: hidden;
  }

  .nav-previous {
    width: 50%;
  }

  .nav-next {
    text-align: right;
    width: 50%;
  }
}
